/*-- This stylesheet is dedicated to the animation of the color rect's on main --*/
/*-- Joacim Granberg - tegelochhatt.se --*/
/* https://reactcommunity.org/react-transition-group/css-transition */

/**
Colors:
Pink: rgb(237, 21, 255)
Blue: rgb(0, 74, 227)
Green: rgb(21, 209, 0)
**/

/*-- Skeleton --*/
/*---------------
.class {
    positionfromtop: where;
    positionfromleft: where;
    size: _;
    size: _;
    color: _;
    background-color: _;
    transition: prop duration;
}
----------------*/

/* DEFINITIONS */
$pink-color: rgb(237, 21, 255);
$blue-color: rgb(0, 74, 227);
$green-color: rgb(21, 209, 0);
$rect-width: 37vw; 
$rect-height: 29vw; // set to vw temporary for easy testing Should be vhÂ§
/* final position for rects in middle from top and right */
$rect-width-tablet: 80vw; // because the media screen is small we set welcome rects to big and stacked
$rect-width-tablet-static: 80vw; // static page contains text and should fill a large portion of the screen
$rect-height-tablet: 60vw; // in order to stack we set height to small. vw instead of vh because scaling issues else hardcode
$rect-height-tablet-static: 70vh;
$top-middle:  20vh;
$right-middle: 32vw;
$left-middle: 32vw;
$top-middle-tablet:  20vh;
$right-middle-tablet: 10vw;
$left-middle-tablet: 10vw;

/* Blue final in welcome position */
$blue-top-welcome-final: 34vw;
$blue-right-welcome-final: 25vw;
$blue-left-welcome-final: 39vw;
/* Blue final in welcome position (TABLET) */
$blue-top-welcome-final-tablet: 73vh; // same calc as pink with start of 44;
$blue-right-welcome-final-tablet: 5vw;

/* Pink final in welcome position */
$pink-top-welcome-final: 15vh;
$pink-left-welcome-final: 7vw;
/* Pink final in welcome position (TABLET) */
$pink-top-welcome-final-tablet: 44vh; // calculated 15 + (height of rect) 34 - (overlapping) 5 = 44;
$pink-left-welcome-final-tablet: 10vw;

/* Green final in welcome position */
$green-top-welcome-final: 0vh;
$green-right-welcome-final: 5vw;
$green-left-welcome-final: 55vw;

/* Green final in welcome position (TABLET) */
$green-top-welcome-final-tablet: 15vh; // give some space to menu
$green-right-welcome-final-tablet: 5vw;

//animations
$slow-welcome-anim: all 700ms;
$fast-exit-anim: all 350ms;

/*--------------*/
/* Rect define */
.rect-anim {
    width: $rect-width;
    height: $rect-height;
}

/*--------------*/
/* Green rect */
.green {
    position: absolute;
    background-color: $green-color;
    top: $green-top-welcome-final;
    left: $green-left-welcome-final;
}
/*--------------*/
/* Welcome animation*/
.green-transition-enter,
.green-transition-appear {
    top: -100vh;
}
.green-transition-enter-active,
.green-transition-appear-active {
    top: $green-top-welcome-final;
    transition: $slow-welcome-anim;
}
.green-transition-exit {
    top: $green-top-welcome-final;
    right: $green-left-welcome-final;
}
.green-transition-exit-active {
    top: $top-middle;
    left: $left-middle;
    transition: $slow-welcome-anim;
}

/* Gather green on top*/
.green-transition-ontop-enter,
.green-transition-ontop-appear {
    top: $green-top-welcome-final;
    left: $green-left-welcome-final;
    
}
.green-transition-ontop-enter-done,
.green-transition-ontop-appear-done {
    top: $top-middle;
    left: $left-middle;
    
    transition: $fast-exit-anim;
}

/* Gather green under*/
.green-transition-under-enter,
.green-transition-under-appear {
    top: $green-top-welcome-final;
    left: $green-left-welcome-final;
    background-color: $green-color;
}
.green-transition-under-enter-done,
.green-transition-under-appear-done {
    top: $top-middle;
    left: $left-middle;
    background-color: $green-color;
    transition: $fast-exit-anim;
}

/*--------------*/
/* Pink rect */
.pink {
    position: absolute;
    background-color: $pink-color;
    top: $pink-top-welcome-final;
    left: $pink-left-welcome-final;
}
.pink-transition-enter,
.pink-transition-appear {
    left: -47vw;
}
.pink-transition-enter-active,
.pink-transition-appear-active {
    top: $pink-top-welcome-final;
    left: $pink-left-welcome-final;
    transition: $slow-welcome-anim;
}
.pink-transition-exit {
    top: $pink-top-welcome-final;
    left: $pink-left-welcome-final;
}
.pink-transition-exit-active {
    top: $top-middle;
    left: $right-middle;
    transition: $slow-welcome-anim;
}
/* Gather pink on top*/
.pink-transition-ontop-enter,
.pink-transition-ontop-appear {
    top: $pink-top-welcome-final;
    left: $pink-left-welcome-final;
   
}
.pink-transition-ontop-enter-done,
.pink-transition-ontop-appear-done {
    top: $top-middle;
    left: $left-middle;
    
    transition: $fast-exit-anim;
}

/* Gather pink under*/
.pink-transition-under-enter,
.pink-transition-under-appear {
    top: $pink-top-welcome-final;
    left: $pink-left-welcome-final;
    background-color: $pink-color;
}
.pink-transition-under-enter-done,
.pink-transition-under-appear-done {
    top: $top-middle;
    left: $right-middle;
    background-color: $pink-color;
    transition: $fast-exit-anim;
}

/*--------------*/
/* Blue rect */
.blue {
    position: absolute;
    background-color: $blue-color;
    top: $blue-top-welcome-final;
    left: $blue-left-welcome-final;
}
.blue-middle {
    background-color: $blue-color;
    top: $top-middle;
    left: $left-middle;
}
.blue-transition-enter,
.blue-transition-appear {
    top: 100vh;
    left: $blue-left-welcome-final;
}
.blue-transition-enter-active,
.blue-transition-appear-active {
    top: $blue-top-welcome-final;
    left: $blue-left-welcome-final;
    transition: $slow-welcome-anim;
}
.blue-transition-exit {
    top: $blue-top-welcome-final;
    left: $blue-left-welcome-final;

}
.blue-transition-exit-active {
    top: $top-middle;
    left: $left-middle;

    transition: $slow-welcome-anim;
}
.blue-transition-exit-done {
    top: $top-middle;
    left: $left-middle;
}

/* Gather blue on top*/
.blue-transition-ontop-enter,
.blue-transition-ontop-appear {
    top: $blue-top-welcome-final;
    left: $blue-left-welcome-final;
}
.blue-transition-ontop-enter-active,
.blue-transition-ontop-appear-active {
    top: $top-middle;
    left: $left-middle;
    transition: $fast-exit-anim;
}
.blue-transition-ontop-enter-done,
.blue-transition-ontop-appear-done {
    top: $top-middle;
    left: $left-middle;
    transition: $fast-exit-anim;
}

/* Gather blue under*/
.blue-transition-under-enter,
.blue-transition-under-appear {
    top: $blue-top-welcome-final;
    left: $blue-left-welcome-final;
}
.blue-transition-under-enter-done,
.blue-transition-under-appear-done {
    top: $top-middle;
    left: $left-middle;
    transition: $fast-exit-anim;
}

/*--------------*/
/* Button styles */

.btn {
    position: relative;
    background-color: transparent;
    border: none;
    font-size: 1.2vh;
    font-family: Verdana;
    font-weight: 600;
    padding: 5px;
    margin-right: 4vw;
    color: white;
    :active{
        border: none;
        border-radius: 0px;
    }
    :hover{
        border: none;
    }
    z-index: 100;
    :focus{
    outline: 0 !important;
    }
}
.button-blue {
    z-index: 100;
    :focus{
    outline: 0 !important;
    }
}
.button-pink {
    position: relative;
    background-color: transparent;
    border: none;
    font-family: Verdana;
    font-weight: 600;
    padding: 5px;
    margin-right: 50px;
    color: white;
    :focus{
        outline:0;
    }
    z-index: 100;
}
.button-green {
    position: relative;
    background-color: transparent;
    border: none;
    font-family: Verdana;
    font-weight: 600;
    padding: 5px;
    margin-right: 50px;
    color: white;
    z-index: 100;
}
.active {
    border-radius: 0px;
    border-bottom: solid 4px white;
}

/*------------*/
/* Static Rectangles */
.static-blue-rect {
    position: absolute;
    top: $top-middle;
    left: $left-middle;
    background-color: $blue-color;
    
}

.static-pink-rect {
    position: absolute;
    top: $top-middle;
    left: $left-middle;
    background-color: $pink-color;
    
}
.static-green-rect {
    position: absolute;
    top: $top-middle;
    left: $left-middle;
    background-color: $green-color;
    
}

/* Video */
.background-video {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -3;
}

body{
    background-color: black;
}

.static-page {
    padding: 14% 9%;
}

#text-row {
    min-height: 15vw;
}

.information-text {
    font-size: 1.1vw;
    color: white;
    text-align: left;
    z-index: 5;
}

.img-wrapper {
    width: 100%;
    height: 100%;
}

.logo-image {
    margin-left: 1vw;
    width: 500px;
    height: 136px;
}

.raster {
    position: absolute;
    width: $rect-width;
    height: $rect-height;
    top: 4vh;
    left: 4vh;
    z-index: -1;
}
.static-page-link{
    color: white !important; 
    text-align: left;
    font-size: 20px;
    font-weight: 600;
    z-index: 5;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border-bottom: solid 3px white;
    border-radius: 0px;
}
a:hover{
    text-decoration: none;

}
.icon-pic{
    width: 220px;
    height: 81px;
}

